/* start h_menu */
.h_menu {
  float: right;
  margin-top: 0.6em;
}
.flexy-menu {
  width: 100%;
  margin: 0;
  padding: 0;
  position: relative;
  float: left;
  list-style: none;
}
.flexy-menu li {
  display: inline;
  font-size: 13px;
  margin: 0;
  padding: 0;
  float: left;
  line-height: 20px;
  position: relative;
}
.flexy-menu > li > a {
  display: block;
  color: #FFF;
  padding: 12px 30px 12px 30px;
  font-family: 'Open Sans', sans-serif;
  font-size: 16px;
  font-weight: 400;
  text-shadow: 0 1px 0 rgba(255, 255, 255, 0.2);
  transition: background-color 0.5s ease-out;
  -webkit-transition: background-color 0.5s ease-out;
  transition: 0.5s all;
  -webkit-transition: 0.5s all;
  -moz-transition: 0.5s all;
  -o-transition: 0.5s all;
}
.flexy-menu > li > a:hover {
  color: #55C1E5;
}
.flexy-menu li.right {
  float: right;
}
.flexy-menu ul, .flexy-menu ul li ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: none;
  position: absolute;
  z-index: 99999;
  width: 200px;
  background: #333;
  -webkit-box-shadow: 0 1px 1px rgba(0, 0, 0, 0.3);
          box-shadow: 0 1px 1px rgba(0, 0, 0, 0.3);
}
.flexy-menu ul {
  top: 60px;
  left: 0;
}
.flexy-menu ul li ul {
  top: 0;
  left: 100%;
}
.flexy-menu ul li {
  clear: both;
  width: 100%;
  border: 0;
  font-size: 13px;
}
.flexy-menu ul li a {
  text-transform: capitalize;
  font-size: 13px;
  padding: 10px 20px;
  width: 100%;
  color: #dedede;
  text-decoration: none;
  display: inline-block;
  float: left;
  clear: both;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -webkit-transition: color .2s linear,background .2s linear;
  transition: color .2s linear,background .2s linear;
}
.flexy-menu > li .arrow {
  position: absolute;
  color: #dedede;
  top: 20px;
  right: 8px;
  font-size: 17px;
}
.flexy-menu i {
  line-height: 20px !important;
  margin-right: 6px;
  font-size: 18px;
  float: left;
}
.flexy-menu > li.showhide {
  display: none;
  width: 100%;
  height: 40px;
  cursor: pointer;
  color: #dedede;
}
.flexy-menu > li.showhide span.title {
  margin: 10px 0 0 15px;
  float: left;
  font-size: 1.3em;
  color: #FFF;
}
.flexy-menu > li.showhide span.icon {
  margin: 12px 10px;
  float: right;
}
.flexy-menu > li.showhide .icon em {
  margin-bottom: 3px;
  display: block;
  width: 20px;
  height: 3px;
  background: #fff;
}
.h_menu > ul > li.showhide > span.icon > em:nth-child(4) {
  display: none;
}
