.da-slider {
  width: 100%;
  height: 350px;
  position: relative;
  overflow: hidden;
  -webkit-transition: background-position 1s ease-out 0.3s;
  transition: background-position 1s ease-out 0.3s;
  text-align: center;
  margin-top: 10em;
}
.da-slide {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0px;
  left: 0px;
  text-align: left;
  font-family: 'Open Sans', sans-serif;
}
.da-slide-current {
  z-index: 1000;
}
.da-slider-fb .da-slide {
  left: 100%;
}
.da-slider-fb .da-slide.da-slide-current {
  left: 0px;
}
.da-slide h2,
.da-slide p,
.da-slide .da-link,
.da-slide .da-img {
  position: absolute;
  opacity: 0;
  left: 110%;
}
.da-slider-fb .da-slide h2,
.da-slider-fb .da-slide p,
.da-slider-fb .da-slide .da-link {
  left: 10%;
  opacity: 1;
}
.da-slider-fb .da-slide .da-img {
  left: 60%;
  opacity: 1;
}
.da-slide h2 {
  color: #FFFFFF;
  width: 77%;
  top: 0px;
  white-space: nowrap;
  z-index: 10;
  font-weight: 600;
  text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.1);
  font-size: 3.5em;
  text-align: center;
  font-family: 'Open Sans', sans-serif;
  text-transform: uppercase;
}
.da-slide p {
  top: 110px;
