.sr-only {
  border: 0;
  clip: rect(0, 0, 0, 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}
.sr-only-focusable:active, .sr-only-focusable:focus {
  clip: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  position: static;
  width: auto;
}
/*-- //button-effect --*/
/*-- Nav style -- */
@media screen and (max-width: 892px) {
#menu_hide {
    visibility: visible;
}
}
@media screen and (min-width: 893px) {
#menu_hide {
    visibility: hidden;
}
}
.menu_set {
  padding-left: 36%;
  color: #bd1509;
}
.cd-stretchy-nav {
  position: fixed;
  z-index: 2;
  top: 50px;
  right: 0.5%;
  pointer-events: none;
}
.cd-stretchy-nav .stretchy-nav-bg {
  /* this is the stretching navigation background */
  position: absolute;
  z-index: 1;
  top: 0;
  right: 0;
  width: 55px;
  height: 55px;
  border-radius: 30px;
  -webkit-box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
  -o-box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
  -ms-box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
  -webkit-transition: height 0.2s, box-shadow 0.2s;
  -webkit-transition: height 0.2s, -webkit-box-shadow 0.2s;
  transition: height 0.2s, -webkit-box-shadow 0.2s;
  transition: height 0.2s, box-shadow 0.2s;
  transition: height 0.2s, box-shadow 0.2s, -webkit-box-shadow 0.2s;
}
.cd-stretchy-nav.nav-is-visible {
  pointer-events: auto;
}
.cd-stretchy-nav.nav-is-visible .stretchy-nav-bg {
  height: 100%;
  -webkit-box-shadow: 0 6px 30px rgba(0, 0, 0, 0.2);
  -ms-box-shadow: 0 6px 30px rgba(0, 0, 0, 0.2);
  -o-box-shadow: 0 6px 30px rgba(0, 0, 0, 0.2);
  box-shadow: 0 6px 30px rgba(0, 0, 0, 0.2);
}
.cd-nav-trigger {
  position: absolute;
  z-index: 3;
  top: 0;
  right: 0;
  height: 55px;
  width: 55px;
  border-radius: 50%;
  /* replace text with image */
  overflow: hidden;
  white-space: nowrap;
  color: transparent !important;
  pointer-events: auto;
}
.cd-nav-trigger span, .cd-nav-trigger span::after, .cd-nav-trigger span::before {
  position: absolute;
  width: 23px;
  height: 2px;
  background-color: #111010;
}
.cd-nav-trigger span {
  /* middle line of the hamburger icon */
  left: 50%;
  top: 50%;
  bottom: auto;
  right: auto;
  -webkit-transform: translateX(-50%) translateY(-50%);
  transform: translateX(-50%) translateY(-50%);
  -webkit-transition: background-color 0.2s;
  transition: background-color 0.2s;
}
.cd-nav-trigger span::after, .cd-nav-trigger span::before {
  /* top and bottom lines of the hamburger icon */
  content: '';
  top: 0;
  left: 0;
  -webkit-transition: -webkit-transform 0.2s;
  transition: -webkit-transform 0.2s;
  transition: transform 0.2s;
  transition: transform 0.2s, -webkit-transform 0.2s;
}
.cd-nav-trigger span::before {
  -webkit-transform: translateY(-7px);
  transform: translateY(-7px);
}
.cd-nav-trigger span::after {
  -webkit-transform: translateY(7px);
  transform: translateY(7px);
}
.no-touch .cd-nav-trigger:hover ~ .stretchy-nav-bg {
  -webkit-box-shadow: 0 6px 30px rgba(0, 0, 0, 0.2);
  -o-box-shadow: 0 6px 30px rgba(0, 0, 0, 0.2);
  -ms-box-shadow: 0 6px 30px rgba(0, 0, 0, 0.2);
  box-shadow: 0 6px 30px rgba(0, 0, 0, 0.2);
}
.nav-is-visible .cd-nav-trigger span {
  background-color: transparent;
}
.nav-is-visible .cd-nav-trigger span::before {
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
}
.nav-is-visible .cd-nav-trigger span::after {
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}
.cd-stretchy-nav ul {
  position: relative;
  z-index: 2;
  padding: 60px 0 10px;
  visibility: hidden;
  -webkit-transition: visibility 0.3s;
  transition: visibility 0.3s;
  text-align: right;
}
.cd-stretchy-nav ul li {
  display: block;
}
.cd-stretchy-nav ul a {
  position: relative;
  display: block;
  width: 55px;
  height: 40px;
  padding: 0;
  color: rgba(255, 255, 255, 0.7);
  -webkit-transition: color 0.2s;
  transition: color 0.2s;
  text-align: left;
}
.cd-stretchy-nav ul a::after {
  position: absolute;
  right: 19px;
  top: 50%;
  -webkit-transform: translateY(-50%) scale(0);
