/*-- gallery --*/
.item-desc {
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
  position: absolute;
  text-transform: uppercase;
  text-align: center;
  padding: 1em;
  z-index: 1;
  bottom: 1em;
  left: 1em;
  right: 1em;
}
/* Filter controls */
.simplefilter li, .multifilter li, .sortandshuffle li {
  color: #1d1b1b;
  cursor: pointer;
  display: inline-block;
  padding: 0px 23px 9px;
  font-size: 18px;
  margin: 0 0.4em;
  letter-spacing: 1px;
  transition: 0.5s all;
  -webkit-transition: 0.5s all;
  -o-transition: 0.5s all;
  -ms-transition: 0.5s all;
  -moz-transition: 0.5s all;
  text-transform: capitalize;
}
.simplefilter li.active {
  color: #00c5b5;
  border-bottom: 2px solid #00c5b5;
}
.simplefilter li:hover {
  color: #00c5b5;
  border-bottom: 2px solid #00c5b5;
  transition: 0.5s all;
  -webkit-transition: 0.5s all;
  -o-transition: 0.5s all;
  -ms-transition: 0.5s all;
  -moz-transition: 0.5s all;
}
.filtr-item img {
  width: 100%;
}
/* Shuffle and sort controls */
select {
  padding: 1rem 1rem;
  text-transform: uppercase;
}
.sortandshuffle .shuffle-btn {
  background-color: #dec800;
}
.sortandshuffle .sort-btn {
  background-color: #de0000;
}
.sortandshuffle .sort-btn.active {
  background-color: #9d0000;
}
ul.simplefilter {
  text-align: center;
  margin-bottom: 15px;
  border-bottom: 1px solid #eee;
}
.item-info {
  padding-top: 70px;
}
.item-type-double .item-hover {
  z-index: 5;
  -webkit-transition: all 300ms ease-out;
  transition: all 300ms ease-out;
  opacity: 0;
  cursor: pointer;
  display: block;
  text-decoration: none;
  text-align: center;
}
.item-type-double .item-info {
  z-index: 10;
  color: #ffffff;
  vertical-align: middle;
  position: relative;
  z-index: 5;
  text-align: center;
  width: 100%;
}
.item-type-double .item-info .headline {
  font-size: 15px;
  width: 90%;
  margin: 0 auto;
}
.item-type-double .item-info .line {
  height: 2px;
  width: 0%;
  margin: 15px auto;
  background-color: #ffffff;
  -webkit-transition: all 300ms ease-out;
  transition: all 300ms ease-out;
}
.filtr-item {
  padding-top: 30px;
}
.jm-item {
  padding: 15px;
  display: inline-block;
  text-align: left;
}
.jm-item-wrapper {
  position: relative;
  padding: 7px;
  background: #FFFFFF;
  border: 1px solid #eee;
}
.jm-item-image {
  position: relative;
  overflow: hidden;
  width: 340px;
  height: 300px;
}
.jm-item-image img {
  display: block;
  width: 100%;
}
.jm-item-title {
  text-align: center;
  background: rgba(0, 175, 255, 0.9);
  color: #FFFFFF;
  font-size: 20px;
  font-weight: bold;
  letter-spacing: 3px;
}
.jm-item-overlay {
  background: #000;
  opacity: 0;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  -webkit-transition: opacity 0.5s ease-in 0s;
  transition: opacity 0.5s ease-in 0s;
}
.jm-item-wrapper:hover .jm-item-overlay {
  opacity: 0.3;
}
.jm-item-button {
  text-align: center;
  position: absolute;
  left: 36%;
  margin-left: -25px;
}
.jm-item-button a {
  background: rgba(234, 135, 8, 0.9);
  text-transform: uppercase;
  color: #FFFFFF;
  font-size: 15px;
  letter-spacing: 2px;
  padding: 10px 15px;
  -webkit-transition: all 0.2s ease-in 0s;
  transition: all 0.2s ease-in 0s;
  text-decoration: none !important;
  display: block;
}
.jm-item-button a:hover {
  background: #FFE;
}
a.agile-its-property-title {
  font-size: 22px;
  display: block;
